body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #282c34;
    color: white;
  }
  
  .App {
    text-align: center;
  }
  
  .App-header {
    background-color: #282c34;
    padding: 20px;
    color: white;
  }
  
  .search-bar {
    margin: 20px;
  }
  
  .search-bar input {
    padding: 10px;
    font-size: 16px;
    width: 300px;
    border: none;
    border-radius: 5px;
  }
  
  .search-bar button {
    padding: 10px;
    font-size: 16px;
    margin-left: 10px;
    border: none;
    border-radius: 5px;
    background-color: #61dafb;
    color: #282c34;
    cursor: pointer;
  }
  
  .movie-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
  }
  
  .movie {
    margin: 10px;
    text-align: left;
    background-color: #444;
    padding: 10px;
    border-radius: 5px;
  }
  
  .movie img {
    max-width: 100%;
    border-radius: 5px;
  }
  
  .movie h3 {
    margin: 10px 0;
  }
  
  .error {
    color: red;
    margin: 20px;
  }